/* --- home main --- */
.home {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.H1 {
  color: #f5f5f5;
  font-size: 3rem;
  line-height: 1em;
}

.H2 {
  color: #1d1f22;
  font-size: 2rem;
  line-height: 1em;
}

.H3 {
  color: #fff1d9;
  font-size: 1.5rem;
  line-height: 1em;
}

.H4 {
  color: #fff1d9;
  font-size: 1rem;
  line-height: 1em;
}

.H5 {
  color: #fff1d9;
  font-size: 1rem;
  line-height: 1em;
}

.H6 {
  color: #fff1d9;
  font-size: 1rem;
  line-height: 1em;
}

.p {
  color: #fff1d9;
  font-size: 1rem;
  line-height: 1em;
}

.home {
  background-color: #c1c4cb;
}

.header {
  background-color: #1d1f22;
}

.main {
  background-color: #fff1d9;
}

.left {
  background-color: #ffffff;
}

.right {
  background-color: #ffffff;
}

.score {
  background-color: #fff1d9;
}

.roll__btn {
  background-color: #436bfa;
}

.container {
  max-width: 90em;
  margin-inline: auto;
  text-align: center;
}

header {
  padding-block: 1em;
}

.main,
.score {
  border-radius: 0.5em;
}

.hero {
  margin-top: 0.5em;
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1em;
}
.hero > * {
  flex-basis: 100%;
  border-radius: 1em;
  min-height: 50svh;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.511);
  border: 1px solid rgba(96, 79, 0, 0.171);
}
.hero .H2 {
  margin-top: 1em;
}

.wrapper {
  margin-top: 1em;
}
.wrapper__right, .wrapper__left {
  width: 80%;
  margin-inline: auto;
  margin-block: 1em;
  border-radius: 1em;
  aspect-ratio: 1/1;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
}
.wrapper__right.fist, .wrapper__left.fist {
  background-image: url(../assets/img/fist.webp);
}
.wrapper__right.paper, .wrapper__left.paper {
  background-image: url(../assets/img/paper.webp);
}
.wrapper__right.scissor, .wrapper__left.scissor {
  background-image: url(../assets/img/scissor.webp);
}

.score {
  margin-top: 0.5em;
}
.score .H2 {
  text-align: left;
}

.roll {
  margin-top: 0.5em;
}
.roll__btn {
  display: inline-block;
  padding: 1em 2em;
  border-radius: 1em;
  box-shadow: 1px 1px 5px black;
  cursor: pointer;
  transition: all ease-in-out 0.1s;
}
.roll__btn:hover {
  background-color: #4e74ff;
}
.roll .click {
  transform: translateY(3px);
}

